body {
  background: #222;
}

@media (max-width: 1383px) and (min-width: 800px) {
  body {
    flex-direction: column;
    display: flex;
  }

  #pre {
    order: 1;
  }

  canvas {
    width: 800px;
    height: 600px;
    border: 10px solid #333;
    order: 2;
    margin: 0 calc(50vw - 410px);
  }

  #post {
    order: 3;
  }

  .google-maps {
    display: none;
    position: absolute;
  }

  #gm-place {
    z-index: 1;
  }

  #gm-directions {
    z-index: 2;
  }

  #overlay {
    z-index: 3;
    width: 800px;
    height: 600px;
    background-color: #0009;
    display: none;
    position: absolute;
  }
}

@media (min-width: 1384px) {
  canvas {
    z-index: 0;
    border: 10px solid #333;
    margin: auto;
    position: absolute;
    inset: 0;
  }

  .google-maps {
    display: none;
    position: absolute;
  }

  #gm-place {
    z-index: 1;
  }

  #gm-directions {
    z-index: 2;
  }

  #overlay {
    z-index: 3;
    width: 800px;
    height: 600px;
    background-color: #0009;
    display: none;
    position: absolute;
  }
}

